<template>
  <div id="QuestionMain">

    <div class="questionHeader">

      <h2 class="not-margin">
        #Question_{{questionNumber}}
      </h2>
    </div>
    <div style="text-align:left;margin: 5px 15px 10px 15px;">{{ question.question }}</div>
    <vs-row align="center">
      <vs-col w="9">

        <vs-input
          v-model="answer"
          placeholder="Your answer"
          class="submitElement"
          :loading="loading"
        />
      </vs-col>
      <vs-col w="3">

        <vs-button
          block
          class="submitElement"
          :loading="loading"
          @click="ForwardAnswer"
        >
          Submit
        </vs-button>
      </vs-col>
    </vs-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      answer: ''
    }
  },
  props: ['question', 'loading', 'SubmitAnswer', 'questionNumber', 'bus'],
  mounted () {
    this.bus.$on('reset', this.reset)
  },
  methods: {
    ForwardAnswer () {
      this.SubmitAnswer(this.answer)
    },
    reset () {
      this.answer = ''
    }
  }
}
</script>

